<template lang="pug">
    div(class="page")
        Header
        section(class='build-project')
            div(class='container')
                div(class='title')
                    h2 {{ $t("Build") }} 
                        span {{ $t("your-project") }} 
                div(class='build-content')
                    div(class='alert bg-danger alert-warning alert-dismissible fade show' role='alert' v-if="notSent")
                        p {{ $t("error-message") }}
                        //- button(type='button' class='close' data-dismiss='alert' aria-label='Close')
                            span(aria-hidden='true') &times;
                    div(class='form-area')
                        form(@submit="submitFile" ref="buildForm" enctype="multipart/form-data" action='')
                            .row
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('full-name')" class='form-control' type='text' name="name" v-model="buildFormData.name" required)
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('phone-number')" class='form-control' type='tel' name="phone" v-model="buildFormData.phone" required)
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('company-name')" class='form-control' type='text' name="company" v-model="buildFormData.company")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('email')" class='form-control' type='email' name="email" v-model="buildFormData.email")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        div(class='custom-select-wrapper')
                                            vue-picker(v-model='buildFormData.project_type' class='form-control' :placeholder="$t('project-type')")
                                                vue-picker-option(value='Mobile') Mobile Application
                                                vue-picker-option(value='Web') Web Application
                                                vue-picker-option(value='System' text='System') System
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('country')" class='form-control' type='text' name="country" v-model="buildFormData.country")
                                div(class="col-lg-6")
                                    div(class='custom-range time-range')
                                        .layout-slider
                                            h4.subtitle {{ $t("timeline") }}
                                            vue-slider(ref="slider" v-model="buildFormData.timeline" :min="minTime" :max="maxTime")
                                div(class="col-lg-6")
                                    div(class='custom-range budget-range')
                                        .layout-slider
                                            h4.subtitle {{ $t("budget") }}
                                            vue-slider(ref="slider" v-model="buildFormData.budget" :min="minBudget" :max="maxBudget")
                                div(class="col-lg-12")
                                    div(class='upload')
                                        fa(class="theicon" :icon="['fas', 'upload']")
                                        div(class="custom-file")
                                            input(type='file' class="file-input" id="file" ref="file" name="file" v-on:change="handleFileUpload")
                                            p(class="file-name" v-if="this.buildFormData.file.length < 1") {{ $t("upload-file") }}
                                            p(class="file-name" v-else="this.buildFormData.file.length >= 1") {{ this.buildFormData.file.name }}
                                div(class="col-lg-12")
                                    div(class='form-group button')
                                        button(class='btn hvr-sweep-to-bottom' type='submit') {{ $t("send") }} 
                                div(class="col-lg-12" v-if="sent")
                                    p.successfully {{ successMessage }}
        Footer
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import { VuePicker, VuePickerOption } from "@invisiburu/vue-picker";
import "@invisiburu/vue-picker/dist/vue-picker.min.css";
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/antd.css";
// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "BuildProject",
    components: {
        Header,
        Footer,
        VuePicker,
        VuePickerOption,
        VueSlider,
    },
    data: () => ({
        selected: "",
        successMessage: "",
        sent: false,
        notSent: false,
        buildFormData: {
            name: "",
            phone: "",
            company: "",
            timeline: [0, 2],
            budget: [0, 1000],
            email: "",
            project_type: "",
            country: "",
            file: "",
        },
    }),
    created() {
        this.minTime = 0;
        this.maxTime = 36;
        this.minBudget = 0;
        this.maxBudget = 20000;
        this.enableCross = true;
        this.tooltipMerge = false;
    },
    methods: {
        submitFile(e) {
            e.preventDefault();
            let formData = new FormData();
            formData.append("name", this.buildFormData.name);
            formData.append("phone", this.buildFormData.phone);
            formData.append("company", this.buildFormData.company);
            formData.append("timeline", this.buildFormData.timeline);
            formData.append("budget", this.buildFormData.budget);
            formData.append("email", this.buildFormData.email);
            formData.append("project_type", this.buildFormData.project_type);
            formData.append("country", this.buildFormData.country);
            formData.append("file", this.buildFormData.file);
            axios
                .post("build-project", formData, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                })
                .then((result) => {
                    this.successMessage = result.data.data.message;
                    this.sent = true;
                    this.notSent = false;
                    this.buildFormData.name = "";
                    this.buildFormData.phone = "";
                    this.buildFormData.company = "";
                    this.buildFormData.email = "";
                    this.buildFormData.project_type = "";
                    this.buildFormData.country = "";
                    this.buildFormData.file = "";
                    // console.warn(result);
                    this.$refs.buildForm.reset();
                })
                .catch((error) => {
                    this.notSent = true;
                    this.sent = false;
                    console.log(error);
                });
        },
        handleFileUpload() {
            this.buildFormData.file = this.$refs.file.files[0];
        },
    },
};
</script>

<style lang="scss">
@import "../sass/global";
.vue-slider {
    padding: 7px !important;
}
.vue-slider-dot-tooltip-inner {
    font-size: 12px !important;
    padding: 3px 6px !important;
}
.vue-slider-process {
    background-color: $second_color;
}
.vue-slider-dot-handle {
    border: 7px solid $main_color;
}
.vue-slider-dot-handle-focus {
    box-shadow: 0 0 0 5px rgba(16, 38, 96, 0.25);
}

.vue-slider:hover .vue-slider-process {
    background-color: $second_color;
}
.vue-slider:hover .vue-slider-dot-handle-focus {
    border-color: $main_color;
}
.vue-slider:hover .vue-slider-dot-handle {
    border-color: $main_color;
}
.vue-slider:hover .vue-slider-dot-handle:hover {
    border-color: $main_color;
}
.vue-slider-dot-tooltip-inner {
    font-size: 14px;
    white-space: nowrap;
    padding: 6px 8px;
    color: #fff;
    border-radius: 5px;
    border-color: $main_color;
    background-color: $main_color;
}

.vue-picker__opener {
    padding: 0;
    border: none !important;
    color: #848484;
    &:focus {
        outline: none;
        border: none;
        box-shadow: none;
        .vue-picker__opener-ico {
            @include transform(rotate(180deg));
            @include transition(all ease 0.4s);
        }
    }
}
.vue-picker:not(.vue-picker--has-val) > .vue-picker__opener {
    color: #848484;
}
.vue-picker__dropdown {
    background: var(--col-dd-bg);
    border: 1px solid #e3e3e3;
    top: 105%;
    border-radius: 10px;
    box-shadow: 1px 1px 15px -10px #858585;
}
</style>
